import React, { Component } from 'react';
import KEYSext_view from '../KEYSext_view.txt';
import appConfig from '../appConfig.json';
// import {Link} from 'react-router-dom';
let baseRoute=appConfig.baseRoute;
let baseUrl=appConfig.baseUrl;
export default class Navbar extends Component {
    constructor() {
        super();
        this.KEYSext_view_array = KEYSext_view.split(';');
        this.homePage = appConfig.homePage;
    }
    componentDidMount() {
        let that = this;
        let currentRoute=window.location.href.split('/').pop();
            if(currentRoute=='access-panel'){

            
        $("#search_keys").autocomplete({
            source: function (request, response) {
                var results = $.ui.autocomplete.filter(that.KEYSext_view_array, request.term);
                response(results.slice(0, 15));

            },
            response: function (event, ui) {
                // if (ui.content.length === 0) {
                //     $('#ui-id-1').css('display','block');
                //     $('#ui-id-1').html('<li id="not_found" className="ui-menu-item"><div id="ui-id-18" tabindex="-1" class="ui-menu-item-wrapper">Not Found</div></li>');
                // }
                //  else {

                //     $('#ui-id-1').css('display','none');
                // }
            },
            select: function (event, ui) {
                let obj = appConfig.dropdownSelection;
                Object.entries(obj).map(([key, value]) => {
                    if (value.includes(ui.item.value)) {
                        let root_folder = appConfig.Access_Panels_External_View.root_folder,
                            ext = appConfig.ext;
                        let svgUrl = root_folder + key + ext;
                        let selectedElement = { 'svgUrl': svgUrl, 'text': ui.item.value }
                        that.props.selectedElement(selectedElement);
                    }
                })
            },
        });
        $('#navbar_menu_container li').click(function(){
            let dataId=$(this).data('id');
            if(dataId=="external_view"){
                window.location.href=baseUrl+'/access-panel';
            }
        })
    }
    }
    NavigateExternalView(e){
        window.location.href='/access-panel';
    }
    gotoHomePage(e) {
        e.preventDefault();
      
        window.location.href = baseUrl;
    }
    render() {
        return (
            <React.Fragment>
                <nav className="navbar navbar-80 navbar-expand-lg navbar-light bg-light custom_navbar">
                   {/* <Link className="navbar-brand" to={baseRoute}>Home</Link> 
                    */}
                    <a href={baseUrl} className="home-icon"><i className="fa fa-home" aria-hidden="true"></i></a>
                    <form className="form-inline my-2 my-lg-0">
                            <input className="form-control mr-sm-2 col-12" id="search_keys" type="search" placeholder="Search" aria-label="Search" />
                        </form>
                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav mr-auto" id="navbar_menu_container">
                            <li className="nav-item" data-id="external_view">
                                <button className="btn nav_button_blue col-12" onClick={e=>this.NavigateExternalView(e)}>External View</button>
                            </li>
                            <li className="nav-item">
                                <button className="btn nav_button_blue col-12 " type="button">Antenna</button>
                            </li>
                            <li className="nav-item">
                                <button className="btn nav_button_blue col-12 " type="button">Damage Evaluation</button>
                            </li>
                            <li className="nav-item">
                                <button className="btn nav_button_blue col-12 " type="button">Damage Reporting</button>
                            </li>
                        </ul>
                        <button type="button" className="close close-button-navbar float-right" aria-label="Close">
                            <span aria-hidden="true" onClick={e => this.gotoHomePage(e)}>&times;</span>
                        </button>
                    </div>
                </nav>
            </React.Fragment>
        )
    }
}


