import React, { Component } from 'react';
import svgFunctions from './svgFunctions';
import Snap from 'snapsvg';
import appConfig from '../appConfig.json';
export default class SvgWork extends Component {
    constructor(props){
         super(props);
            this.element;
            this.data;
            this.strokeWidth=1.5;
            this.strokeWidthZero=0;
            this.PrimaryColor='#00a2db';
            this.BlackColor='#000000';
            this.stroke='black';
            this.f_in=this.f_in.bind(this);
            this.f_out=this.f_out.bind(this);  
            this.planePartsClicked=this.planePartsClicked.bind(this);
            this.planeElemenetsArray=["#aircraft_view_all_svg_Illustrator_Symbole_S_FUSS_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_1_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_2_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_LH_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_ENG_LH_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_ENG_RH_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_VERT_STAB_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL",
            "#aircraft_view_all_svg_Illustrator_Symbole_S_FUS_WING_0_Layer0_0_MEMBER_0_FILL"];
            this.layerArray=['#Layer0_1_FILL','#Layer0_2_FILL','#Layer0_3_FILL','#Layer0_4_FILL','#Layer0_5_FILL','#Layer0_6_FILL']
    }
    svgRender() {
        this.element = Snap(this.svgDiv);
        let that = this;
        let modelSvg = appConfig.Access_Panels_External_View.root_folder + appConfig.Access_Panels_External_View.files.Access_Start + appConfig.ext;
        Snap.load(modelSvg, function (data) {
            let svgContainerWidth = $('.custom_container_90').width();
            let svgWidth = $(data.node).attr('width');
            let svgHeight = $(data.node).attr('height');

            // console.log(svgWidth,svgHeight)
            let ratio = parseInt(svgWidth) / parseInt(svgHeight);
            console.log(ratio);
            let calcHeight=svgContainerWidth / ratio;
       
            let screenHeight=parseInt(window.innerHeight)-parseInt($('.custom_navbar').height());
            // console.log(screenHeight);
            if(calcHeight>screenHeight){
                svgContainerWidth=svgContainerWidth*((screenHeight-50)/calcHeight);
                calcHeight=screenHeight-50;
            }
            // console.log(svgContainerWidth,calcHeight);
            $(data.node).width(svgContainerWidth);
            $(data.node).height(calcHeight);

            that.data = data;
            if (that.element) {
                that.element.append(data);
                that.element.hover(that.f_in, that.f_out);
                that.element.click(that.planePartsClicked);
                that.menuListHover();
            }
        });
    }
    menuListHover() {
        let that = this;
        $('text[clip-path="url(#Mask_Mask_6)"] tspan ').on('mouseover', function (e) {
            $(this).attr('fill', that.PrimaryColor);
        })
        $('text[clip-path="url(#Mask_Mask_6)"] tspan ').on('mouseout', function (e) {
            $(this).attr('fill', 'black');
        })
    }
    f_in(e) {
        let that = this;
        let target = e.target;
        if (target.tagName == 'use') {
            let selector = $(target).attr('xlink:href');
            let selector2 = '';
            if (selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_2_FILL" || selector=="#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_1_FILL") {
                selector="#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_0_FILL";

            }
            else if(that.layerArray.includes(selector)){
                selector="#aircraft_view_all_svg_Illustrator_Symbole_S_FUSS_0_Layer0_0_MEMBER_0_FILL";

            }
            else if(selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL" ||
            selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL"){
                selector = "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL";
                selector2 = "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL";
            }
            else if(selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL" ||
            selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL"){
                selector = "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL";
                selector2= "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL";
            }
            if (that.planeElemenetsArray.includes(selector)) {
                $(selector).children('path').css({ 'stroke': that.stroke, 'stroke-width': that.strokeWidth });
                if (selector2 != '') {
                    $(selector2).children('path').css({ 'stroke': that.stroke, 'stroke-width': that.strokeWidth });
                }
                selector='';
                selector2='';
            }
        }
    }
  f_out(e){ 
    let that = this;
    let target = e.target;
    if (target.tagName == 'use') {
        let selector = $(target).attr('xlink:href');
        let selector2 = '';
        if (selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_2_FILL" || selector=="#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_1_FILL") {
            selector="#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_0_FILL";

        }
        else if(that.layerArray.includes(selector)){
            selector="#aircraft_view_all_svg_Illustrator_Symbole_S_FUSS_0_Layer0_0_MEMBER_0_FILL";
        }
        else if(selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL" ||
        selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL"){
            selector = "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL";
            selector2 = "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL";
        }
        else if(selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL" ||
        selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL"){
            selector = "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL";
            selector2= "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL";
        }
        if (that.planeElemenetsArray.includes(selector)) {
            $(selector).children('path').css({ 'stroke-width': that.strokeWidthZero });
            if (selector2 != '') {
                $(selector2).children('path').css({ 'stroke-width': that.strokeWidthZero });
            }
            selector='';
            selector2='';
        }
    }
  }
  planePartsClicked(e){
    let that = this;
    let target = e.target;
    if (target.tagName == 'use') {
        let selector = $(target).attr('xlink:href');
        let selector2 = '';
        if (selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_2_FILL" || selector=="#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_1_FILL") {
            selector="#aircraft_view_all_svg_Illustrator_Symbole_S_NOSE_0_Layer0_0_MEMBER_0_FILL";

        }
        else if(that.layerArray.includes(selector)){
            selector="#aircraft_view_all_svg_Illustrator_Symbole_S_FUSS_0_Layer0_0_MEMBER_0_FILL";
        }
        else if(selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL" ||
        selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL"){
            selector = "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_0_FILL";
            selector2 = "#aircraft_view_all_svg_Illustrator_Symbole_S_WING_RH_0_Layer0_0_MEMBER_1_FILL";
        }
        else if(selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL" ||
        selector == "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL"){
            selector = "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_0_FILL";
            selector2= "#aircraft_view_all_svg_Illustrator_Symbole_S_HOR_STAB_0_Layer0_0_MEMBER_1_FILL";
        }
        if(that.planeElemenetsArray.includes(selector)) {
            let svgInfo=svgFunctions.getSvgUrl(selector);
            that.props.selectedPP(svgInfo);
        }
    }

    if(target.tagName=='tspan'){
        let selector = $(target).text();
        let svgInfo=svgFunctions.getSvgUrl(selector);
  
        svgInfo!=null?that.props.selectedPP(svgInfo):'';
        
    }
  }
  componentDidMount() {
    this.svgRender();
  }
  componentDidUpdate() {
    this.svgRender();
  }
  render() {
    return  <div ref={d=>this.svgDiv=d} />
  }
}