import React, {Component} from 'react';
import ReactHtmlParser from 'react-html-parser';
import classNames from 'classnames';

export class Paragraph extends Component {
  render() {
    let {item} = this.props;
    return(<p className={classNames('fw-4 font-14', {[`${item.class}`]: true })}>{item.text}</p>)
  }
}
export class CenteredText extends Component {
  render() {
    let {item} = this.props;
    return(<p className={classNames('fw-4 font-14 text-center', {[`${item.class}`]: true })}>{item.text}</p>)
  }
}
export class SmallHeading extends Component {
  render() {
    let {item} = this.props;
    return(<div className={classNames('font-600 bold font-16 mt-1 mb-1', {[`${item.class}`]: true })}>{item.text}</div>)
  }
}
export class NoteHeading extends Component {
  render() {
    let {item} = this.props;
    return(<div className={classNames('fw-4 font-16', {[`${item.class}`]: true })}><span style={{color:"#0d72db"}}>Note: </span>{item.text}</div>)
  }
}

export class HTMLComponent extends Component {
  render() {
    let {item} = this.props;
    return(<div className={classNames({[`${item.class}`]: true })}>{ReactHtmlParser (item.html)}</div>)
  }
}
