// pdf open functions 


$('body').delegate('.svg_name_heading tspan','click',function(){
    if($(this).hasClass('downloadpdf')){
        let pdfId=$(this).data('id');
     
        window.open(pdfUrl+pdfId+'.pdf','_blank');
    }
})
$('body').delegate('.pdf_link','click',function(){
    let pdfText=$(this).text();
    let domainpdf=(pdfText.split(' ')[0]).toLowerCase();;
    
    let pdfNum=pdfText.split(' ').pop();

    let pdfUrl = pdfConfig.mainUrl + pdfConfig.pdfFiles[domainpdf] + pdfNum + '.pdf';
   
        window.open(pdfUrl,'_blank');
});

$('body').delegate('.loetr_html','click',function(){
    let htmltext=$(this).text();
    let loetr_link='';
    if(htmltext.includes('TR-FIM')){
        loetr_link = pdfConfig.mainUrl + pdfConfig.htmlPages['TR-FIM'];
    }
    else if(htmltext.includes('TR-AMM')){
         loetr_link = pdfConfig.mainUrl + pdfConfig.htmlPages['TR-AMM'];
    }
    else if(htmltext.includes('TR-AIPC')){
         loetr_link = pdfConfig.mainUrl + pdfConfig.htmlPages['TR-AIPC'];
    }
        window.open(loetr_link,'_blank');
});



