$( document ).ready(function() {
    var winwid = $( window ).width();
    var previousSelctedFunc;
    $( window ).resize(function() {
        winwid = $( window ).width();
        if (winwid >= 992) {
            $(".list-group").hide();
            var height = $(".listing-bars").height();
             height=height<540?550:height;
            $(".listing-bars .listing-list .bar").height(height);
            $('.funt-list').show()
        }
        if (winwid < 992) {
            $(".list-group").show();
            // $(".listing-bars .listing-list .bar").height('');
            // $('.funt-list').hide()
        }
        
      });
    if (winwid >= 992) {
        $(".list-group").hide();
        var height = $(".listing-bars").height();
        height=height<540?550:height;
        $(".listing-bars .listing-list .bar").height(height);
        $('.funt-list').show()
    }
    else {
        if (winwid < 992) {
            $(".list-group").show();
            // $(".listing-bars .listing-list .bar").height('');
            // $('.funt-list').hide()
        }
    }
    if (winwid <= 991) {
        $(".gnd_eng_sys").click(function () {
            // $("#GND_ENG_STOPPED ul").toggle();
            // $("#GND_ENG_STOPPED").toggle();

        });

        $('body').delegate('.panel-default','click',function(e){
            if($(this).attr('id')=="gnd_testing_id")return;
            e.preventDefault();
            if($(this).find('a').attr("data-toggle")===""){
                $('.panel-collapse').removeClass("show");
                $('.panel-default').find('a').attr("data-toggle","");
                 $(this).find('a').attr("data-toggle", "collapse");
            }
            else{
                $('.panel-default').find('a').attr("data-toggle","");
                $('.panel-collapse').removeClass("show");
                
                // $(this).find('a').attr("data-toggle", "");
            }
             
            // $('.panel-collapse').removeClass('show');
            // $(this).find('.panel-collapse').toggleClass('show');
        })
    }
    // collapse on mobile
    $(document).delegate('.funt-head', 'click', function (e) {
        if (winwid < 992) {
            var selectedval = $(this).find('h4').text();
            if (previousSelctedFunc == selectedval) {
                previousSelctedFunc = '';
                $('.funt-list').hide();
                return;
            }
            previousSelctedFunc = selectedval;
            $('.funt-list').hide();
            $(this).siblings('.funt-list').show();
        }
    })




    $('body').delegate('.hamburger', 'click', function () {
        $(".click-mobile").slideToggle("slow");
    })
   
    $('body').delegate('.column-text','click',function(e){
     let id=$(this).data('id');
        let base=appConfig.baseUrl;
        
        base=base+'cautions/'+id;
       
        window.location.href=base;
    })
   
    $('.search-area-container input').click(function(){
      

        $(this).select();
    })

    function settings() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        $('body').css('zoom', 1)
    }

        setTimeout(settings(),500)
   
    
});