import React, {Component} from 'react';
import PropTypes from 'prop-types';
import Elements from '../elements/';

class GeneralCautions extends Component {
  render() {
    let {data} = this.props;

    return (
      <div className="card">
        <div className="card-header">{data.title}</div>
        <div className="card-body left-card customFaults">
        {
          data.values && data.values.map((item, i) => {
            return(<Elements key={i} item={item} />)
          })
        }
        </div>
    </div>
    )
  }
}
GeneralCautions.defaultProps = {
  data: {}
};
GeneralCautions.propTypes = {
  data: PropTypes.object
};
export default GeneralCautions;