import React, { Component } from 'react';
import SvgWork from './SvgWork';
import PlanePartSelect from './PlanePartSelect';


export default class SvgContainer extends Component {
    constructor(props) {
        super(props);
        this.state={
            svgPartLoad:false,
            svgUrl:props.svgUrl,
            text:props.text
        }
        this.selectedPlanePart=this.selectedPlanePart.bind(this);
    }
    componentWillMount(){

    }
    UNSAFE_componentWillReceiveProps(props){
       this.setState({
        svgUrl:props.data.svgUrl,
        text:props.data.text,
        svgPartLoad:true
       })
    }
    selectedPlanePart(svgInfo){
        this.setState({
            svgUrl:svgInfo.svgUrl,
            svgName:svgInfo.name,
            svgPartLoad:true
        })
    }
    render(){
        return(
            <div className='text-center'>
              {!this.state.svgPartLoad && < SvgWork  selectedPP={this.selectedPlanePart}/>}
              {this.state.svgPartLoad && < PlanePartSelect  svgUrl={this.state.svgUrl} svgName={this.state.svgName}  text={this.state.text}/>}
            </div>
        )
    }
}