    getParentElement(elem) {
        let that = this;
        if (elem.prop('tagName') == 'use') {
            return elem;
          
        }
        if(elem.attr('id')=='border')return;
     
        if (elem.prop('tagName') == 'svg' || elem.hasClass('svg-plane-part')) {
            return null;
        }
        if (elem.parent().is('svg')) {
           
                return elem; 
          
           
        }
        else {
            return that.getParentElement(elem.parent());
        }
    }

