import React, { Component } from 'react';
import Navbar from '../navbar/Navbar';
// import KEYSext_view from '../KEYSext_view.txt';
// import appConfig from '../appConfig.json';
// import {Link} from 'react-router-dom';
// let baseRoute=appConfig.baseRoute;
import { deepGet, arrayToObject} from 'Common/functions';
import DisplayFaults from './displayFaults/display';
import DescriptionFaults from './displayFaults/description';
import ActionFaults from './displayFaults/action';
import AssociatedFaults from './displayFaults/associated';
import DisplayFaultJson from '../helperJson/displayFaults.json';


export default class DisplayFault extends Component {
    constructor(props) {
        super(props);
        this.state={
            faultLoaded: true,
            laoding: false,
            display: {},
            description: {},
            action: {},
            associated: {},
            faultName:''
        }
        this.getFaultFromJson=this.getFaultFromJson.bind(this);
    }
    componentDidMount() {
        let faultName = deepGet(this.props, ['match', 'params', 'faultName'], undefined);
        this.getFaultFromJson(faultName);

    }
    settings() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        $('body').css('zoom', 1)
    }
    componentDidUpdate() {
        setTimeout(this.settings(), 500)
        // $('.search-area-container input').select();
    }
    UNSAFE_componentWillReceiveProps(props){
        let NextfaultName = deepGet(props, ['match', 'params', 'faultName'], undefined); 
        if(this.state.faultName!=NextfaultName){
            this.getFaultFromJson(NextfaultName);
        }
    }
    getFaultFromJson(faultName){
        try {
            // let faultName = deepGet(this.props, ['match', 'params', 'faultName'], undefined);
           
            let faultData = DisplayFaultJson.find(f=> f.faultName==faultName);
          
            let display = deepGet(faultData, ['data', 'display'], {});
            let description = deepGet(faultData, ['data', 'description'], {});
            let action = deepGet(faultData, ['data', 'action'], {});
            let associated = deepGet(faultData, ['data', 'associated'], {});
            this.setState({
                display: display,
                description: description,
                action: action,
                associated: associated,
                faultLoaded: true,
                laoding: false,
                faultName:faultName
            });
        } catch (error) {
            this.setState({faultLoaded: false, laoding: false});
        }
    }
  
    render() {
        return (
            <React.Fragment>
                {/* <Navbar /> */}
                <div className="row">
                    <div className="col-md-12 col-sm-12  pd-5">
                        <DisplayFaults data={this.state.display}/>
                        <DescriptionFaults data={this.state.description}/>
                        <ActionFaults data={this.state.action}/>
                        <AssociatedFaults data={this.state.associated}/>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}