import React, {Component} from 'react';
import HomePageUpper from './homePage/HomePageUpper';
import HomePageLower from './homePage/HomePageLower';
// import Routes from './components/routes/Routes';
import  './customScripts/CustomJquery';
export default class App extends Component{
    constructor(props){
        super(props);
        this.state={
            svgUrl:'',
            text:''
        }
    }
    selectedElement(data){
       this.setState({
        svgUrl:data.svgUrl,
        text:data.text
       })
    }
    render(){
        return(
            <div className="container-fluid custom_container_90 col-12 col-lg-12">
                <HomePageUpper {...this.props} />
                {/* <HomePageLower /> */}
            </div>
        )
    }
}