import React, { Component } from 'react';
import PlanePartLoad from './PlanePartLoad';
import PlanePartPopUp from './PlanePartPopup';
import PlaneModel from './PlaneModel';
import svgFunctions from './svgFunctions';
import Snap from 'snapsvg';
import appConfig from '../appConfig.json';
export default class PlanePartSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      svgUrl: props.svgUrl,
      svgName: props.svgName,
      text:props.text,
      selectedElement: false
    }
    this.selectedModelPart = this.selectedModelPart.bind(this);
  }
  UNSAFE_componentWillReceiveProps(props){
    this.setState({
     svgUrl:props.svgUrl,
     text:props.text
    })
 }
  selectedModelPart(data) {
    let that=this;
    that.setState({
      svgUrl: data.svgUrl,
      svgName: data.name
    });
  }
  changeSvgByArrowClicking(data){
   let that=this;
   that.setState({
     svgUrl:data
   })
  }
  render() {
    return (
      <div className="svg-plane-part-container ">
        <div className="row">
          {/* <PlaneModel selectedModelPart={e => this.selectedModelPart(e)}/> */}
          <PlanePartLoad svgUrl={this.state.svgUrl} svgName={this.state.svgName} text={this.state.text} changeSvgByArrowClicking={this.changeSvgByArrowClicking.bind(this)} />
        </div>
      </div>
    )
  }
}