import React, {Component} from 'react';
import classNames from 'classnames';

export class PDFList extends Component {
	render() {
		let {item} = this.props;
		return(
			<div className={classNames('fw-5 font-16 mt-3 mb-3', {[`${item.class}`]: true })} >
			{
				item.items.map(link => {
					return(
						<div className="mb-2" key={link.link}>
							{
								link.text && <h5 className="dib mrg-0 vt mr-4">{link.text}</h5>
							}
							{
								link.link && <h5 className="dib mrg-0 vt blue pdf_link cp">{link.link}</h5>
							}
							{
								link.link2 && <h5 className="dib mrg-0 vt blue pdf_link cp">{link.link2}</h5>
							}
						</div>
					)
				})
			}
			</div>
		);
	}
}

export class DashList extends Component {
	render() {
		let {item} = this.props;
		return(
			<ul className={classNames('dashed fw-4 font-16 mt-4 mb-4', {[`${item.class}`]: true })} >
			{
				item.items.map((link, i) => {
					return(
						<li key={i}>
							{
								link.text && <div className="dib mrg-0 vt mr-4">{link.text}</div>
							}
							{
								link.right && <div className="dib mrg-0 vt">{link.right}</div>
							}
						</li>
					)
				})
			}
			</ul>
		);
	}
}