import React, {Component} from 'react';
import {Paragraph, SmallHeading, NoteHeading, CenteredText, HTMLComponent} from './textElements';
import {PDFList, DashList} from './PDFElements';
import {Image, SVGComponent} from './Images';

export default class Elements extends Component {
  render() {
    let {item} = this.props;
    return(
      item.type==='paragraph' ? <Paragraph item={item} /> : 
      item.type==='centered_text' ? <CenteredText item={item} /> : 
      item.type==='small_heading' ? <SmallHeading item={item} /> : 
      item.type==='note_heading' ? <NoteHeading item={item} /> : 
      item.type==='dash_list' ? <DashList item={item} /> :
      item.type==='pdf_list' ? <PDFList item={item} /> :
      item.type==='picture' ? <Image item={item} /> :
      item.type==='svg_component' ? <SVGComponent item={item} /> :
      item.type==='html' ? <HTMLComponent item={item} /> :
      <div></div>
    )
  }
}