import React, { Component } from 'react';
import PlanePartPopUp from './PlanePartPopup';
import svgFunctions from './svgFunctions';
import Snap from 'snapsvg';
export default class PlanePartLoad extends Component {
  constructor(props) {
    super(props);
    this.state = {
      svgUrl: props.svgUrl,
      svgName: props.svgName,
      selectedElement: false,
      text: props.text
    }
    this.svgUrlValue;
    this.selectedPartText = 'null';
    this.hoverElem;
    this.hoverOutElem;
    this.clickedElem;
    this.f_in = this.f_in.bind(this);
    this.f_out = this.f_out.bind(this);
    this.planePartsClicked = this.planePartsClicked.bind(this);
    
  }
  svgRender() {
    let that = this;
    if (that.svgUrlValue == that.state.svgUrl){
      if(that.element){
         that.selectedTextClicked();
      }
      return;
    } 
    this.element && $('.svg-plane-part').empty();
    that.svgUrlValue = that.state.svgUrl;
    this.element = Snap(this.svgPlanePartDiv);
    Snap.load(that.state.svgUrl, function (data) {
   
      that.data = data;
      if (that.element) {
        that.element.append(data);
       
       // svg dimensions settings

        let svgContainerWidth = $('.svg-plane-part').width();
        let svgWidth = $('.svg-plane-part svg').width();
        let svgHeight = $('.svg-plane-part svg').height();
        let ratio = parseInt(svgWidth) / parseInt(svgHeight);
        let supposedContainerHeight = svgContainerWidth / ratio;
       
        let screenHeight=$(window).height()-$('.custom_navbar').height();
        if(supposedContainerHeight>screenHeight){
            svgContainerWidth=svgContainerWidth*((screenHeight-50)/supposedContainerHeight);
            supposedContainerHeight=screenHeight-50;
        }


        $('.svg-plane-part svg').attr('width', svgContainerWidth + 'px');
        $('.svg-plane-part svg').attr('height', (supposedContainerHeight) + 'px');
        that.element.hover(that.f_in, that.f_out);
        that.element.click(that.planePartsClicked);
        that.selectedTextClicked();
      }
    });
  }
  selectedTextClicked() {
    let that=this;
    if (typeof that.state.text != 'undefined' && that.state.text != '') {
      $('text').map(function (i, ed) {
        if (that.state.text == $(ed).text()) {
          that.setState({
            text: ''
          }, () => {
            that.f_in(ed);
            that.planePartsClicked(ed);
          })
        }
      })
    }
  }
  f_in(e) {
    let that = this;
    let target;
    if (typeof e.target == 'undefined') {
      target = e;
    }
    else {
      target = e.target;
    }
    if (target == that.hoverElem) {
      return;
    }
    that.hoverElem = svgFunctions.PanelsMouseover(target, that.element);
  }
  f_out(e) {
    let that = this;
    let target = e.target;
    if (target == that.hoverOutElem) {
      return;
    }
    that.hoverOutElem = svgFunctions.PanelsMouseout(target, that.element);
  }
  planePartsClicked(e) {
    let that = this;
    let target;
    if (typeof e.target == 'undefined') {
      target = e;
    }
    else {
      target = e.target;
    }
    if (target != that.clickedElem) {
      let clickedElement = svgFunctions.PanelsClicked(target);
      if(!clickedElement)return;
      if(typeof clickedElement.updateSvgUrl !='undefined'){
        that.props.changeSvgByArrowClicking(clickedElement.updateSvgUrl);
        return;
      }
      that.clickedElem = clickedElement.elem;
      that.setState({
        clickedElement: clickedElement,
        selectedElement: true
      })
    }
  }
  componentDidMount() {
    let that = this;
    this.svgRender();
  }
  componentDidUpdate() {
    let that = this;
    this.svgRender();
  }
  UNSAFE_componentWillReceiveProps(props) {
    this.setState({
      svgUrl: props.svgUrl,
      svgName: props.svgName,
      text: props.text,
      selectedElement: false
    })
  }
  render() {
    return (
      <React.Fragment>
        <div className="svg-plane-part col-xs-12 col-md-9 col-lg-9" ref={d => this.svgPlanePartDiv = d} />
        {this.state.selectedElement && <PlanePartPopUp clickedElement={this.state.clickedElement} />}
      </React.Fragment>
    )
  }
}