import React, { Component } from 'react';
import svgFunctions from './svgFunctions';
import Snap from 'snapsvg';
import appConfig from '../appConfig.json';
export default class PlaneModel extends Component {
    constructor(props) {
        super(props);
        this.state={
       
        }
        this.element;
        this.data;
        this.strokeWidth = 1.5;
        this.strokeWidthZero = 0;
        this.PrimaryColor = '#00a2db';
        this.BlackColor = '#000000';
        this.stroke = 'black';
        this.f_in = this.f_in.bind(this);
        this.f_out = this.f_out.bind(this);
        this.planePartsClicked = this.planePartsClicked.bind(this);
    }
    svgRender() {
        if(typeof this.element!='undefined')return;
        this.element = Snap(this.planeModelDiv);
        let that = this;
        let modelSvg = appConfig.Access_Panels_External_View.root_folder + appConfig.Access_Panels_External_View.files.Aircraft_view_all + appConfig.ext;
        Snap.load(modelSvg, function (data) {
            that.data = data;
            if (that.element) {
                that.element.append(data);
                let svgContainerWidth = $('.plane-model-container').width();
                let svgWidth = $('.plane-model-container svg').width();
                let svgHeight = $('.plane-model-container svg').height();
                let ratio = parseInt(svgWidth) / parseInt(svgHeight);
                let supposedContainerHeight = svgContainerWidth / ratio;
                $('.plane-model-container svg').attr('width', svgContainerWidth + 'px');
                $('.plane-model-container svg').attr('height', (supposedContainerHeight) + 'px');
                that.element.hover(that.f_in, that.f_out);
                that.element.click(that.planePartsClicked);
            }
        });
    }
    f_in(e) {
        let that = this;
        let target = e.target;
        let getPart=svgFunctions.PlaneModelMouseOver(target); 
        if(typeof getPart !='undefined'){
            // $('.plane-part-text-container').css('visibility','visible');
            $('.plane-part-text-container').removeClass('vis_hid')
            $('.plane-part-text-container').text(getPart.name)
        }
        
    }
    f_out(e) {
        let that = this;
        let target = e.target;
        svgFunctions.PlaneModelMouseOut(target);
        $('.plane-part-text-container').addClass('vis_hid')
        // $('.plane-part-text-container').css('visibility','hidden');
        // $('.plane-part-text-container').text('')
    }
    planePartsClicked(e) {
        let that = this;
        let target = e.target;
        let getPart=svgFunctions.PlaneModelClicked(target);
        that.props.selectedModelPart(getPart);
        
    }
    componentDidMount() {
        this.svgRender();
      
    }
    componentDidUpdate() {
        this.svgRender();
    }
    UNSAFE_componentWillReceiveProps(props){
    }
    render() {
        return (
            <div className="col-xs-12 col-md-2 col-lg-2 fit-height mb-3">
                <div className="col-xs-12 col-md-8 col-lg-12 border-shadow m-auto">
                      <div className="plane-model-container" ref={d=>this.planeModelDiv=d} >
                </div>
                <div className="plane-part-text-container vis_hid">
                    tetsst
                </div>
                </div>
              
            </div>
        )
    }
}