import React, { Component } from 'react';
import Navbar from '../navbar/Navbar';
import UnitHtml from './UnitHtml';
import { deepGet, arrayToObject} from 'Common/functions';
import GeneralFaults from './engineFaults/general';
import PossibleFaults from './engineFaults/possible';
import EngineFaultJson from '../helperJson/engineFaults.json';


export default class EngineApuEcsFault extends Component {
    constructor(props) {
        super(props);
        this.state={
            faultLoaded: true,
            laoding: false,
            general: {},
            possible: {},
            faultName:''
        }
        this.getFaultFromJson=this.getFaultFromJson.bind(this);
    }
    componentDidMount() {
        let faultName = deepGet(this.props, ['match', 'params', 'faultName'], undefined);
        this.getFaultFromJson(faultName);

    }
    settings() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        $('body').css('zoom', 1)
    }
    componentDidUpdate() {
        setTimeout(this.settings(), 500)
        // $('.search-area-container input').select();
    }
    UNSAFE_componentWillReceiveProps(props){
        let NextfaultName = deepGet(props, ['match', 'params', 'faultName'], undefined); 
        if(this.state.faultName!=NextfaultName){
            this.getFaultFromJson(NextfaultName);
        }
    }
    getFaultFromJson(faultName){
        try {
          
            console.log('fault name ',faultName);
            let faultData = EngineFaultJson.find(f=> f.faultName==faultName);
            let general = deepGet(faultData, ['data', 'general'], {});
            let possible = deepGet(faultData, ['data', 'possible'], {});
            this.setState({
                general: general,
                possible: possible,
                faultLoaded: true,
                laoding: false,
                faultName:faultName
            });
        } catch (error) {
            this.setState({faultLoaded: false, laoding: false});
        }
    }
    render() {
        return (
            <React.Fragment>
                <div className="row">
                    <div className="col-md-12 col-sm-12 pd-5">
                        <GeneralFaults data={this.state.general}/>
                        <PossibleFaults data={this.state.possible}/>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}