import React, {Component} from 'react';
import Unit from '../Unit';
import classNames from 'classnames';

export class Image extends Component {
	render() {
		let {item} = this.props;
		return(
			<div className={classNames('fw-5 font-16 mt-3 mb-3', {[`${item.class}`]: true })}>
				<img src={item.link} alt="image" />
			</div>
		);
	}
}

export class SVGComponent extends Component {
	render() {
		let {item} = this.props;
		return(<Unit name={item.name} />);
	}
}
